<template>
  <layout help="投诉?" help-link="/order/help" :routes="routes">
    <div slot="filter">
       <mt-checklist
          title="购买平台"
          v-model="filter.platforms"
          :options="platforms">
        </mt-checklist>
        <mt-checklist
          title="付款方式"
          v-model="filter.payments"
          :options="payments">
        </mt-checklist>
        <mt-checklist
          title="任务类型"
          v-model="filter.types"
          :options="types">
        </mt-checklist>
    </div>
  </layout>
</template>

<script>
import Layout from '@/components/layout';

export default {
  name: 'OrderLayout',
  components: { Layout },
  data() {
    return {
      filter: {
        platforms: ['淘', 'J东', '拼'],
        payments: ['商家垫付', '买手垫付', '浏览任务'],
        types: ['精刷任务', '预约任务', '复购任务'],
      },
      platforms: ['淘', 'J东', '拼'],
      payments: ['商家垫付', '买手垫付', '浏览任务'],
      types: ['精刷任务', '预约任务', '复购任务'],
    };
  },
  computed: {
    routes() {
      return this.$store.getters.orderRoutes;
    },
  },
};
</script>



